.App {
  text-align: center;
  background-color: #f7f7f7;
}

.Portfolio{
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: relative;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

input, .champs select{
  width:50%; 
  font-size:35px; 
  padding:1em 10%;
  clear:both; 
  text-align:right;
}

input:focus
{
  border: blue 10px;
}

label{background:black; padding:3px 6px; font-size:40px; margin-left:1px; font-weight:normal; clear:both;}